.table_container {
  overflow: auto;
  position: relative;
  max-height: calc(100vh - 4em);
  max-height: max(calc(100vh - 4em), 8em);
  margin: 1em 0;
}

table.dwm_main {
  float: left;
  clear: both;
  width: 100%;
  height: 100%;
  border-spacing: 0;
  border-collapse: separate;
  border-color: #dddddd;
  border-width: 0px;
  border-style: solid;
  border-radius: 5px;
}
.dwm_main {
  height: 100%;
  width: 100%;
}

.dwm_main thead tr:first-child th:first-child {
  border-top-left-radius: 5px;
}

.dwm_main thead th:first-child {
  z-index: 610;
}

.dwm_main thead th.first_last,
.dwm_main tfoot th.first_last {
  width: 1px;
}

.dwm_main thead th,
.dwm_main tfoot th {
  font-size: small;
  font-weight: normal;
  vertical-align: top;
  padding: 4em;
  color: #0b263b;
  background-color: #ffffff;
  background-clip: padding-box;
}

.dwm_main th,
.dwm_main td {
  border-right: 1px solid #dddddd;
}

th {
  white-space: nowrap;
  vertical-align: middle;
}

.dwm_main thead tr:last-child th {
  border-bottom: 1px solid #fca588;
}

.dwm_main thead th {
  top: 0;
}

.dwm_main th a:hover {
  color: #0b263b;
  text-decoration: underline;
}

.dwm_main th a:visited {
  color: #0b263b;
}

.dwm_main th a {
  text-decoration: none;
  font-weight: normal;
}

.dwm_main a,
.dwm_main .booked span.saving {
  display: block;
  height: 100%;
  width: 100%;
  min-height: inherit;
  word-break: break-all;
  word-break: break-word;
  hyphens: auto;
}

.dwm_main thead th,
.dwm_main tfoot th {
  font-size: small;
  font-weight: normal;
  color: #0b263b;
}

th {
  white-space: nowrap;
}

/* .dwm_main tbody tr:hover th {
  background-color: ;
} */

.dwm_main th:first-child {
  left: 0;
}

.dwm_main thead th,
.dwm_main th:first-child {
  position: -webkit-sticky;
  position: sticky;
  z-index: 600;
}

tbody tr:nth-child(2n + 1) th {
  background-color: #efefef;
}

.dwm_main tbody th {
  text-align: left;
}

.new {
  border-left: 1px solid #dddddd;
}

body {
  font-size: small;
  color: #0b263b;
  font-family: Arial, "Arial Unicode MS", Verdana, sans-serif;
}

/* .dwm_main tbody tr:hover th a {
  color: #ffffff;
} */

tbody th a:hover {
  color: #0b263b;
  text-decoration: underline;
}

tbody th a:visited {
  color: #0b263b;
}

.dwm_main tbody a {
  padding: 0.2em;
  box-sizing: border-box;
}

tbody th a {
  display: inline-block;
  text-decoration: none;
  font-weight: normal;
}

.table_container {
  overflow: auto;
  position: relative;
  max-height: max(calc(100vh - 20em), 8em);
  margin: 1em 0;
}

.dwm_main td:hover.new,
.dwm_main td.new_hover {
  background-color: #fca588;
}

tr:nth-child(2n + 1) td.new,
.all_rooms tr:nth-child(2n + 1) td {
  background-color: #efefef;
}

.dwm_main td,
.dwm_main tbody td + th {
  border-right: 1px solid #e4e4e4;
}

.dwm_main td {
  position: relative;
  padding: 0;
  border-bottom: 0;
  border-right: 0;
}

td,
th {
  vertical-align: top;
}

td.new a,
a.new_booking {
  font-size: medium;
  text-align: center;
}

a:hover {
  background-color: #fca588;
  color: #0b263b;
  text-decoration: underline;
  font-weight: bold;
}

a:visited {
  color: #0b263b;
  text-decoration: none;
  font-weight: bold;
}

